export default {
	data() {
		return {
			globalUser:{}
		}
	},
	onShow(){
		var globalUser = this.getGlobalUser("globalUser")
		this.globalUser = globalUser				
	},
	methods: {		
		operator(){
			var globalUser =this.getGlobalUser("globalUser")
			uni.showActionSheet({
				itemList:["查看头像","修改头像"],
				success(res){
					var index =res.tapIndex
					if(index == 0){
						// 查看头像
						var faceArr=[]
						faceArr.push(globalUser.avatarUrl)
						uni.previewImage({
							urls:faceArr,
							current:faceArr[0]
						})
					}
					else if(index==1){
						// 上传头像
						uni.chooseImage({
							count:1,
							sizeType:['origina','compressed'],
							sourceType:['album','camera'],
							success(res){
								// 获得临时路径
								var tempFilePth = res.tempFilePaths[0]
								uni.navigateTo({
									url:'../meFace/meFace?tempFilePath=' + tempFilePth
								})
							}
						})
						uni.navigateTo({
							url:'../meFace/meFace'
						})
					}
				}
			})
		},
		clearStorage(){
			uni.clearStorage();
			uni.showToast({
				title:"清理缓存成功",
				mask:false,
				duration:1600
			})
		},
		// 退出登录
		logout(){
			var globalUser = this.getGlobalUser("globalUser")
			uni.request({
				url: this.$common.serverUrl+'/user/logout?userId=' + globalUser.id + '&&'+ this.$common.qqId,
				method:'POST',
				success: res => {
					if(res.data.status ==200){
						uni.removeStorageSync('globalUser')
						uni.switchTab({
							url:'../me/me'
						})
					}
				}
			});	
		}
	}
}
